<template>
  <div class="manage">
    <div class="manage-header">
      <el-button
        type="primary"
        @click="
          dialogVisible = true;
          modelType = true;
        "
        >添加用户信息</el-button
      >

      <el-form ref="searchForm" :inline="true" :model="searchForm">
        <el-form-item style="margin-bottom: 0px">
          <el-input
            v-model="searchForm.name"
            placeholder="请输入搜索内容"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin-bottom: 0px">
          <el-button type="primary" @click="onSearch">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>

    <el-dialog
      title="添加用户信息"
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      width="40%"
    >
      <el-form
        ref="userForm"
        :model="userForm"
        :rules="rules"
        :inline="true"
        label-width="80px"
      >
        <el-form-item label="姓名" prop="name">
          <el-input v-model="userForm.name" placeholder="请输入姓名"></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input v-model="userForm.age" placeholder="请输入年龄"></el-input>
        </el-form-item>

        <el-form-item label="性别" prop="sex">
          <el-select v-model="userForm.sex" placeholder="请选择性别">
            <el-option label="男" :value="1"></el-option>
            <el-option label="女" :value="0"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="出生日期" prop="birth">
          <el-date-picker
            v-model="userForm.birth"
            type="date"
            value-format="yyyy-MM-DD"
            placeholder="选择日期"
          >
          </el-date-picker>
        </el-form-item>

        <el-form-item label="地址" prop="addr" style="width: 100%">
          <el-input
            v-model="userForm.addr"
            placeholder="请输入地址"
            style="width: 100%"
          ></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="danger" @click="resetForm('userForm')">重置</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
        <el-button
          @click="
            dialogVisible = false;
            resetForm('userForm');
          "
          >取 消</el-button
        >
      </span>
    </el-dialog>

    <el-table
      :data="tableData"
      stripe
      style="height: 100%; margin-top: 20px; overflow: auto"
    >
      <el-table-column prop="name" label="姓名"></el-table-column>
      <el-table-column prop="sex" label="性别">
        <template slot-scope="scope">
          <span>{{ scope.row.sex ? "男" : "女" }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="age" label="年龄"></el-table-column>
      <el-table-column prop="birth" label="出生日期"></el-table-column>
      <el-table-column prop="addr" label="地址"></el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="minin"
            @click="
              handelEdit(scope.row);
              modelType = false;
            "
            >编辑</el-button
          >
          <el-button type="danger" size="minin" @click="handelDelete(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <div class="manage-footer">
      <div></div>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getUser, addUser, editUser, delUser } from "@/api";

export default {
  name: "User",

  data() {
    return {
      dialogVisible: false,
      searchForm: {
        name: "",
      },
      userForm: {
        name: "",
        age: "",
        sex: "",
        birth: "",
        addr: "",
      },
      rules: {
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        age: [{ required: true, message: "请输入年龄", trigger: "blur" }],
        sex: [{ required: true, message: "请选择性别", trigger: "change" }],
        birth: [{ required: true, message: "选择出生日期", trigger: "change" }],
        addr: [{ required: true, message: "请输入地址", trigger: "blur" }],
      },
      tableData: [],
      modelType: true, //true表示新增 false代表修改
      total: 0, //数据总条数
      pageData: { page: 1, limit: 10 },
    };
  },
  methods: {
    onSubmit() {
      this.$refs.userForm.validate((valid) => {
        if (valid) {
          //valid表示表单内容中该填写的是否填写完
          if (this.modelType) {
            //true 新增
            addUser(this.userForm).then(() => {
              this.getUserList();
            });
          } else {
            //false 修改
            editUser(this.userForm).then(() => {
              this.getUserList();
            });
          }
          this.resetForm("userForm");
          this.dialogVisible = false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then((_) => {
          this.resetForm("userForm");
          done();
        })
        .catch((_) => {});
    },
    handelEdit(row) {
      this.modelType = false;
      this.dialogVisible = true;
      this.userForm = JSON.parse(JSON.stringify(row));
    },
    handelDelete(row) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          delUser({ id: row.id }).then(() => {
            this.$message({
              type: "success",
              message: "删除成功!",
            });
            this.getUserList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    getUserList() {
      getUser({ params: { ...this.searchForm, ...this.pageData } }).then(
        (data) => {
          this.tableData = data.list;
          this.total = data.count || 0;
        }
      );
    },
    handlePage(value) {
      //返回页码数
      this.pageData.page = value;
      this.getUserList();
    },
    onSearch() {
      this.getUserList();
    },
  },

  mounted() {
    this.getUserList();
  },
};
</script>


<style lang="less" scoped>
.manage {
  max-height: 800px;
  height: 90%;
  .manage-header {
    display: flex;
    justify-content: space-between;
  }

  .manage-footer {
    display: flex;
    justify-content: space-between;
  }
}
</style>